@import "https://fonts.googleapis.com/css2?family=Cardo:ital@1&family=Raleway:wght@400;500;700;900&display=swap";
@import "https://cdn.jsdelivr.net/npm/swiper@8/swiper-bundle.min.css";
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  text-decoration: none;
  list-style-type: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (max-width: 425px) {
  .container {
    padding: 0 20px;
  }
}

.main {
  background: url("bg.a5ebfd83.png") center / cover no-repeat;
  position: relative;
}

.main:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: .8;
  background: #252b49;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.main .header {
  z-index: 4;
  position: relative;
}

.main .header__wrapper {
  max-height: 101px;
  border-bottom: 1px solid #babbc5;
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

@media (max-width: 425px) {
  .main .header__wrapper {
    align-items: center;
  }
}

.main .header__wrapper .logo a {
  padding: 35px 0 34px;
  display: block;
}

@media (max-width: 425px) {
  .main .header__wrapper .logo a {
    padding: 18px 0;
  }
}

.main .header__wrapper .logo a svg {
  width: 185px;
  height: 27px;
}

@media (max-width: 1024px) {
  .main .header__wrapper .logo a svg {
    height: 25px;
  }
}

@media (max-width: 425px) {
  .main .header__wrapper .logo a svg {
    width: 103px;
    height: 14px;
  }

  .main .header__wrapper .menu {
    width: 55px;
    height: 55px;
    align-items: center;
    display: flex;
  }
}

.main .header__wrapper .menu .burger--check {
  display: none;
}

@media (max-width: 425px) {
  .main .header__wrapper .menu .burger--check {
    opacity: 0;
    width: 56px;
    height: 56px;
    z-index: 3;
    cursor: pointer;
    display: block;
    position: absolute;
  }
}

.main .header__wrapper .menu .burger--check:hover + .burger .burger__item {
  background-color: #e71414;
}

.main .header__wrapper .menu .burger--check:checked + .burger .burger__item:nth-child(1) {
  margin-bottom: 0;
  transform: rotateZ(45deg)translate(5px, 5px);
}

.main .header__wrapper .menu .burger--check:checked + .burger .burger__item:nth-child(2) {
  background-color: #fff0;
  transition: all .1s ease-in-out;
}

.main .header__wrapper .menu .burger--check:checked + .burger .burger__item:nth-child(3) {
  margin-bottom: 0;
  transform: rotateZ(-45deg)translate(2px, -2px);
}

.main .header__wrapper .menu .burger {
  display: none;
}

@media (max-width: 425px) {
  .main .header__wrapper .menu .burger {
    width: 24px;
    margin: 0 auto;
    transition: all .3s ease-in-out;
    display: block;
  }

  .main .header__wrapper .menu .burger__item {
    width: 100%;
    height: 3px;
    background-color: #fff;
    margin-bottom: 4px;
    transition: all .3s ease-in-out;
    display: block;
  }
}

.main .header__wrapper .menu .burger:hover .burger__item {
  background-color: #ec7979;
}

.main .header__wrapper .menu-list {
  height: 100%;
  justify-content: space-between;
  display: flex;
}

@media (max-width: 425px) {
  .main .header__wrapper .menu-list {
    z-index: 12;
    padding: 0 20px;
    transition: all .3s ease-in-out;
    display: none;
    position: fixed;
  }
}

