    z-index: 10;
    text-align: left;
    width: 100%;
    position: relative;
  }
}

.main .header__wrapper .menu-list__item-link {
  width: 100%;
  height: 100%;
  text-transform: uppercase;
  color: #fff;
  border-bottom: 3px solid #0000;
  padding-bottom: 38px;
  font-size: 13px;
  font-weight: 700;
  transition: all .3s ease-in-out;
  display: block;
}

