  z-index: 10;
  width: 100%;
  height: 100vh;
  background: url("bg.a5ebfd83.png") top / cover no-repeat;
  display: block;
  top: 83px;
  left: 0;
}

.main .header__wrapper .menu .burger--check:checked ~ .menu-list:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: .8;
  background: #252b49;
  position: absolute;
  top: 0;
  left: 0;
}

